import React from "react";

const img_na =
  "https://www.sahilgraphics.com/wp-content/themes/shahil/assets/" +
  "images/image-not-found.jpg";

export default class Article extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      img_src: props.info.urlToImage,
      source: props.info.source.name,
      title: props.info.title,
      description: props.info.description,
      url: props.info.url,
      content: props.info.content,
      fullData: props.info,
      strData: JSON.stringify(props.info),
      cached: false,
      tab: props.tab
    };
  }

  componentDidMount() {
    console.log("new article");
  }

  componentWillUnmount() {
    console.log("destroying article");
  }

  saveArticle = () => {
    this.setState({
      cached: true
    });
  };

  /* prettier-ignore */
  render() {
    return (
      <div className="article">
        <img src={this.state.img_src != null ? this.state.img_src : img_na}></img>
        <p>
          <b>{this.state.title + " " + this.state.source}</b> <br/>
          {this.state.description} <br/>
        </p>
        <p>
          {this.state.content}
          {/* {this.state.showContent ? this.state.content : null} */}
          {/* {this.state.strData} */}
        </p>
        <p align="right">
          <a href={this.state.url} target="_blank" rel="noreferrer">
            original article</a> <br />
          {this.state.tab ? null :
            <button onClick={this.saveArticle}>save</button>
          }
        </p>
      </div>
    );
  }
}
